.p-container {
  height: 1850px;
  padding: 20px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #202C39;
}

.p-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: auto;
  grid-gap: 1rem;
}

.card {
  margin: 10px 20px;
  padding: 1rem;
};
